@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300;600&family=Montserrat:wght@300;600&display=swap');

* {
    margin: 0;
    padding: 0;
}

body {
    background: #fff;
    color: #222;
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
}

.wrapper {
    width: 1280px;
    margin: 50px auto;
}

header {
    position: relative;
}

header .logo {
    font-weight: 600;
    font-style: 20px;
}

header ul.nav {
    float: right;
    list-style: none;
}

header ul.nav li {
    display: inline;
    margin-left: 25px;
    cursor: pointer;
    transition: opacity 500ms ease;
}

header ul.nav li:hover {
    opacity: 0.5;
}

header .shopCartButton {
    float: right;
    cursor: pointer;
    transition: color,  transform 500ms ease;
}

header .shopCartButton:hover,
header .shopCartButton.active {
    color: #dc3d3d;
    transform: scale(1.5);
}

header .shopCart {
    position: absolute;
    top: 30px;
    right: 0;
    width: 450px;
    background: #f1f1f1;
    -webkit-box-shadow: -5px -5px 5px -5px rgba(34, 60, 80, 0.6) inset;
    -moz-box-shadow: -5px -5px 5px -5px rgba(34, 60, 80, 0.6) inset;
    box-shadow: -5px -5px 5px -5px rgba(34, 60, 80, 0.6) inset;
    z-index: 1000;
    padding: 20px;
    padding-bottom: 0;
}

header .shopCart .empty h2 {
    font-style: 20px;
    margin-bottom: 20px;
}

header .shopCart .item {
    width: 100%;
    float: left;
    margin-bottom: 10px;
}

header .shopCart .item img {
    width: 100px;
    float: left;
    margin-right: 20px;
}

header .shopCart .item h2 {
    font-style: 20px;
    margin-bottom: 10px;
}

header .shopCart .item b {
    color: #5fa36a;
    font-weight: 600;
}

header .shopCart .item .deleteIcon {
    color: #202020;
    float: right;
    position: relative;
    top: -25px;
    cursor: pointer;
    transition: color,  transform 500ms ease;
}

header .shopCart .item .deleteIcon:hover {
    color: #dc3d3d;
    transform: scale(1.5);
}

header .shopCart .summa {
    color: #5fa36a;
    float: left;
    width: 100%;
    font-weight: 600;
    font-size: 20px;
    margin-bottom: 20px;
}

header .presentation {
    margin: 50px 0;
    background: url('./img/bg.jpg') no-repeat;
    width: 100%;
    height: 500px;
    background-size: cover;
    background-position: center center;
    background-blend-mode: multiply;
    background-color: #bcbcbc;
    position: relative;
}

header .presentation::after {
    content: 'Лучшие товары для вашего дома';
    position: absolute;
    top: 100px;
    left: 50px;
    width: 300px;
    font-size: 40px;
    font-weight: 600;
    color: #fff;
}

header .presentation::before {
    content: 'по низким ценам';
    position: absolute;
    top: 250px;
    left: 50px;
    width: 300px;
    font-size: 20px;
    font-weight: 300;
    color: #fff;
}

.categories div {
    display: inline-block;
    background: #f2f2f2;
    border-radius: 50px;
    padding: 10px 20px;
    margin-bottom: 25px;
    margin-right: 15px;
    cursor: pointer;
    border: 1px solid transparent;
    transition: all 500ms ease;
}

.categories div:hover {
    border-color: silver;
    transform: scale(1.1);

}

main {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-between;
}

main .item {
    width: 30%;
    margin-bottom: 50px;
    background: #eceaea;
    position: relative;
    padding-bottom: 20px;
}

main .item img {
    width: 100%;
    border-radius: 10px 10px 0 0;
    transition: transform 500ms ease;
}

main .item img:hover {
    transform: scale(1.05);
}

main h2, 
main p, 
main b {
    margin: 10px 20px;
    color: #333;
}

main b {
    color: #5fa36a;
}

main .addToCard {
    position: absolute;
    right: 20px;
    bottom: 20px;
    background: #ca5252;
    width: 35px;
    height: 35px;
    text-align: center;
    line-height: 35px;
    color: #fff;
    border-radius: 50%;
    cursor: pointer;
    font-weight: 600;
    transition: transform 500ms ease;
} 

main .addToCard:hover {
    transform: scale(1.5) translateY(-5px);
}

footer {
    text-align: center;
    margin-top: 100px;
}